export default function PageNotFound() {
    return (
        <article className="page">
            <Info />
            <Link title="return home" url="/"/>
        </article>
    );
}

const Link = ({ title, url}) => {
    return (
        <a className="link" href={url}>
            <div className="link-title">
                <h3 class="unselectable">{title}</h3>
            </div>
        </a>
    );
};

const Info = () => {
    return (
        <section>
            <h2 class="unselectable">error 404</h2>
            <p>
                This page does not exist!
            </p><br/>
            <p>
                I don't know how you ended up here, but you are not in the right place. Click the button below to return to the homepage.
            </p>
        </section>
    );
};